import React from 'react'
import "./Header.css"
import SearchIcon from '@material-ui/icons/Search';
import HomeIcon from '@material-ui/icons/Home';
import FlagIcon from '@material-ui/icons/Flag';
import SubscriptionsOutlinedIcon from '@material-ui/icons/SubscriptionsOutlined';
import StorefrontOutlinedIcon from '@material-ui/icons/StorefrontOutlined';
import SupervisedUserCircleIcon from '@material-ui/icons/SupervisedUserCircle';
import {Avatar, IconButton} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ForumIcon from '@material-ui/icons/Forum';
import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {useStateValue} from "./StateProvider"

function Header() 
{
	const [{ user },dispatch] = useStateValue();
	return (
	<div class = "header">
		<div class = "header__left">
			<img src="https://facebookbrand.com/wp-content/uploads/2019/04/f_logo_RGB-Hex-Blue_512.png"/>
			<div class="header__input">
				<SearchIcon />
				<input placeholder = "Search Facebook" type="text"/>
			</div>
		</div>	

		<div class = "header__center">
			<div class="header__option header__option--active">
				<HomeIcon fontSize="large"></HomeIcon>
			</div>
			<div class="header__option">
				<FlagIcon fontSize="large"></FlagIcon>
			</div>
			<div class="header__option">
				<SubscriptionsOutlinedIcon fontSize="large"></SubscriptionsOutlinedIcon>
			</div>
			<div class="header__option">
				<StorefrontOutlinedIcon fontSize="large"></StorefrontOutlinedIcon>
			</div>
			<div class="header__option">
				<SupervisedUserCircleIcon fontSize="large"></SupervisedUserCircleIcon>
			</div>
		</div>
		<div class = "header__right">
			<div class="header__info">
				<Avatar src = {user.photoURL}/>
				<h4>{user.displayName}</h4>
			</div>

			<IconButton>
				<AddIcon></AddIcon>
			</IconButton>
			<IconButton>
				<ForumIcon></ForumIcon>
			</IconButton>
			<IconButton>
				<NotificationsActiveIcon></NotificationsActiveIcon>
			</IconButton>
			<IconButton>
				<ExpandMoreIcon></ExpandMoreIcon>
			</IconButton>
		</div>
	</div>)
}

export default Header;